<template>
  <div class="notice">
    <h3 class="notice-brief"> <span class="notice-show" @touchstart="show"></span></h3>
    <div class="notice-content" v-show="isShow" @touchmove.prevent>
      <div class="notice-close" @touchstart="hidden">
        <i class="close-button iconfont icon-close"></i>
      </div>
      <h4 class="notice-title">8.8</h4>
      <p class="notice-text">
        <br>
        <br>
        2<br>
        &nbsp;&nbsp;&nbsp;&nbsp;8912
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isShow: Boolean
  },
  methods: {
    show() {
      this.$emit('show')
    },
    hidden() {
      this.$emit('hidden')
    }
  }
}
</script>

<style lang="stylus" scoped>

  .notice
    .notice-brief
      position: relative
      height: 30px
      line-height: 30px
      text-align: center
      background: #555
      font-size: 15px
      color: #b5b5b5
      z-index: 3
      .notice-show
        font-size: 14px
        color: #efb336
    .notice-content
      position: absolute
      left: 0
      top: 0
      padding: 50px 20px 20px
      width: 90%
      box-sizing: border-box
      margin: 100px 5%
      border-radius: 4px
      z-index: 100
      background: #fff
      .notice-close
        position: absolute
        right: 0
        top: 0
        width: 50px
        height: 50px
        line-height: 50px
        text-align: center
        .close-button
          font-size: 18px
      .notice-title
        line-height: 24px
        font-size: 15px
        font-weight: bold
        color: #f55669
      .notice-text
        line-height: 24px
        font-size: 14px
        color: #666

</style>



